.go-up {
   position: relative;
   bottom: 3rem;
   left: 45%;
   display: flex;
   justify-content: flex-start;
   text-align: center;
   z-index: 10;
}

.up {
    background-color: var(--white);
    width: 45px;
    height:45px;
    margin: 5px;
    padding: 10px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    
}
@media all and (min-width: 768px) {
    .go-up {
         margin-left: 30px;
    }
}
